body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*BASE STYLES*/
.side-bar {
	top:unset;
	margin-left: 0px;
	height: calc(100vh);
	background-color: #062a49;
}
.side-bar-menu-cont {
	background-color: #062a49;
	width: 150px;
	position: absolute;
	top:0px;
	height: 100vh;
	overflow-y: hidden;
	overflow-x: hidden;
}
.side-bar-menu-controls {
	display: flex;
}
.side-bar-menu-more-actions>.action-row {
	margin-top: 15px;
	margin-left: 7.5px;
	gap: 7.5px;
}
.side-bar-menu-more-actions>.action-row>.action-button {
	width: 35px;
	height: 35px;
	background-color: transparent;
	filter: unset;
}
.side-bar-menu-more-actions>.action-row>.action-button:hover {
	background-color: #04223c;
}
.side-bar-menu-more-actions>.action-row>.action-button>img {
	height: 17.5px;
    width: 17.5px;
    margin-top: 7.5px;
}
.side-bar.collapsed {
	width: 55px;
}
.side-bar-menu-cont.collapsed {
	width: 55px;
}
.side-bar.collapsed>.menu-button {
	width: 35px;
	margin-left: 10px;
}
.side-bar>.menu-button {
	width: 130px;
	margin-left: 10px;
}
.action-row#side-bar-bottom-buttons {
	position: fixed;
	bottom: 10px;
	width: 150px;
}
#side-bar-bottom-buttons {
	display: flex;
	flex-direction: column-reverse;
}
.side-bar-menu-cont.collapsed>.action-row#side-bar-bottom-buttons>.action-button {
	width: 35px;
}
.side-bar-menu-cont>.action-row#side-bar-bottom-buttons>.action-button {
	width: 130px;
}
.side-bar-menu-cont.collapsed>.action-row#side-bar-bottom-buttons>.action-button>div {
	color: rgba(0, 0, 0, 0);
	width: 0px;
}
#side-bar-bottom-buttons>.action-button {
	background-color: transparent;
	filter: none;
	display: flex;
	margin-left: 10px;
	margin-right: 10px;
}
#side-bar-bottom-buttons>.action-button:hover {
	background-color: #04223c;
}
.action-button.side-bar-menu:hover {
	background-color: #04223c;
}
#side-bar-bottom-buttons>.action-button>img {
	height: 20px;
	width: 20px;
	margin-top: 5px;
    margin-left: 7.5px;
}
#side-bar-bottom-buttons>.action-button>div {
	margin-left: 5px;
}
.side-bar.collapsed>.menu-button>div{
	pointer-events: none;
}

.menu-button>img {
	/*filter: invert(20%) sepia(22%) saturate(844%) hue-rotate(167deg) brightness(99%) contrast(87%);*/
}
.menu-button>img:hover {
	/*filter: invert(24%) sepia(15%) saturate(683%) hue-rotate(167deg) brightness(89%) contrast(87%);*/
}
.side-bar.collapsed>.menu-button.selected,
.side-bar>.menu-button.selected {
	border-left: 2px solid #43bc90;
	border-radius: 2.5px;
	background-color: #04223c;
}
.side-bar>.menu-button {
	color:#fff;
}
.side-bar>.menu-button:hover {
	background-color: #04223c;
}
.side-bar.collapsed>.menu-button.selected>img,
.side-bar>.menu-button.selected>img {
	margin-left: 6px;
}
.action-button.side-bar-menu {
	
}


.left-align {
	text-align: left;
}
.right-align {
	text-align: right;
}
.center-align {
	text-align: center;
}

.action-button.floating {
	height: 50px;
	width: 50px;
	line-height: 45px;
	font-size: 30px;
	border-radius: 30px;
}

.fab-container.bottom-left {
	display: flex;
	flex-direction: column;
	gap: 10px;
	position: absolute;
	bottom:15px;
	right:15px;
}
.fab-container.top-left {
	display: flex;
	flex-direction: row;
	gap: 10px;
	position: absolute;
	top:0px;
	right:15px;
}

.fab-container.toggled>.action-button.floating {
  	transform: rotate(45deg);
}
.fab-container.bottom-left>.action-row{
	display: flex;
	flex-direction: column;
	margin-bottom: 10px;
	align-items: center;
}
.fab-container.top-left>.action-row{
	display: flex;
	flex-direction: row;
	align-items: center;
}

.fab-container>.action-row>.action-button {
	height: 40px;
	width: 40px;
	line-height: 45px;
	font-size: 30px;
	border-radius: 30px;
}
.fab-container>.action-row>.action-button>img {
	height: 25px;
	width: 25px;
}

.app-container {
	position:absolute;
	left:180px;
	height: calc(100vh - 20px);
	width: calc(100vw - 180px);
	overflow-y: overlay;
	top:20px;
}
.app-container#collapsed {
	position:absolute;
	left:80px;
	height: calc(100vh - 20px);
	width: calc(100vw - 80px);
	overflow-y: hidden;
}







.filter-row {
	display: flex;
	gap: 10px;
}

.filter-button {
	padding: 5px;
	background-color: rgb(160, 204, 198);
	border-radius: 15px;
	padding-left: 10px;
	padding-right: 10px;
	border: 1.5px solid rgb(118, 172, 164);
	cursor: pointer;
	display: flex;
	gap: 10px;
}

.filter-button.selected {
	background-color: rgb(118, 172, 164);
}

.filter-selected-x {
	/*efefef*/
}
.filter-button-img {
	height: 10px;
}
.filter-selected-x:hover {
	font-weight: 800;
}

.filter-button:hover {
	background-color: rgb(118, 172, 164);
}

.quote-sections {
	display: flex;
	flex-direction: row;
	margin-top: 1.5vh;
}

.section.quote-section.open>.section-list {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: calc(100vw - 200px);
}

.section-card {
	width: 15%;
	height: 17.5vh;
	flex-direction: column;
	align-items: center;
	justify-items: center;
	display: flex;
	
}

.quote-info {
	min-height: 50%;
	max-height: 50%;
}

.quote-actions {
	background-color: #f1f1f1;
	display: grid;
	width: 100%;
	justify-content: center;
	grid-template-columns: 40% 40%;
	padding-top: 10px;
	padding-bottom: 10px;
	min-height: 30% !important;
	max-height: 30% !important;
}

.section-card>.action-row>.action-button {
	width: unset;
}

.quote-info {
	width: 90%;
	margin: 5px;
	display: flex;
	flex-direction: column;
	align-items: start;
	justify-content: start;
}

.quote-info-title-min {
	height: 10px;
	color: rgb(80, 80, 80);
	font-style: italic;
	font-weight: 550;
}

.quote-info>.quote-info-title {
	font-size: 30px;
	font-weight: 700;
	color: rgb(60, 60, 60);
	margin-top: 0px;
	padding-top: 0px;
}

.quote-info-status {
	font-style: italic;
	font-weight: 550;
	color: rgb(50, 50, 50);
}

#clear-filter-button-cont {
	margin-bottom: 10px;
	display: none;
}
#clear-filter-button {
	width: 100px;
}

/*QUOTE DETAILED VIEW CARD*/
#quote-card>.card-content {
	display: flex;
	flex-direction: column;
	gap: 5px;
}
#quote-card {
	width: 50vw;
	height: 70vh;
}
.quote-card-header {
	display: flex;
	flex-direction: column;
}
.quote-made-date {
	font-style: italic;
	color: rgb(60, 60, 60);
	align-self: start;
}
.quote-job-name>.header-text {
	font-size: 25px;
	text-align: left;
}


.quote-info-section-card {
	filter: unset;
	border: 2px solid #062a49;
	max-height: 32.5vh;
	min-height: 21.75vh;
}
.quote-info-section-card>.card-menubar {
	border-top-right-radius: 5px;
	border-top-left-radius: 5px;
}
.quote-info-section-card>.card-menubar>.card-title {
	position: relative;
}

#quote-card-actions {
	height: 3vh;
	align-content: center;
}
#quote-card-actions>.action-button {
	height: 100%;
	line-height: 30px;
	font-size: 20px;
}

.quote-form {
	display: flex;
	flex-direction: column;
}

#quote-card-client-info>.card-content {
	align-items: start;
}

#quote-card-client-info>.card-content>.form-info-item {
	display: grid;
	grid-template-columns: 32.5% 67.5%;
	margin-top: 5px;
	margin-bottom: 5px;
}
#quote-card-client-info>.card-content>.form-info-item>.form-info-title {
	align-self: center;
	font-size: 1.125em;
}
#quote-card-client-info>.card-content>.form-info-item>input {
	width: 100%;
	padding-top: 7px;
	padding-bottom: 7px;
}

.quote-info-section-card>.card-content>.action-row>.dropdown-cont {
	width: 400px;
}

.quote-pricing-table {
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: 25% 25% 25% 25%;
	justify-items: center;
}

.quote-pricing-table-entry {
	padding: 0;
	margin: 0;
	margin-top: 10px;
}

.quote-pricing-table-entry>.form-info-item {
	display: grid;
	grid-template-columns: 75% 25%;
}

.quote-pricing-table-header {
	text-align: left;
}

/*QUOTE SCHEDULE PAGE*/

.quote-month-schedule {
	border-radius: 10px;
	height: 100%;
}

.month-header>.header-text {
	text-align: left;
	margin-left: 10px;
	color: rgb(60, 60, 60)
}

.month-view {
	border: 1px solid #efefef;
	background-color: #fff;
	height: calc(100% - 5vh);
	border-radius: 10px;
	width: 100%;
}

.month-headers {
	display: grid;
	grid-template-columns: repeat(7, 14%);
	background-color: #fff;
	height: 40px;
	font-size: 27.5px;
	line-height: 35px;
	align-items: center;
	justify-content: center;
	border-bottom: 2px solid #efefef;
}
.month-headers {
	background-color: transparent;
}
.month-row-cont {
	height: calc(100% - 4vh);
}
.month-day-header {
	border-left: 1px solid #efefef;
	border-right: 1px solid #efefef;
}
.month-day-header.first {
	border-left: 0px solid #efefef;
}
.month-day-header.last {
	border-right: 0px solid #efefef;
}

.month-row-cont {
	display: grid;
	grid-auto-rows: 20%;
}
.month-row {
	display: grid;
	grid-template-columns: repeat(7, 14%);
	border-bottom: 2px solid #efefef;
	align-items: center;
	justify-content: center;
	height: 100%;
}
.month-row-day {
	border-left: 1px solid #efefef;
	border-right: 1px solid #efefef;
	height: 100%;
}
.month-row-day.first {
	border-left: 0px;
}
.month-row-day.last {
	border-right: 0px;
}
.month-row.last {
	border-bottom: 0px;
}

/*ACTIVITY CENTER*/
.activity-center {
	display: flex;
	gap: 10px;
}
#notifications-card {
	min-height: 92.5vh !important;
	max-height: 92.5vh !important;
}
#daily-quotes {
	filter: unset;
	border: 1px solid #ccc;
	max-height: 27.5vh;
	min-height: 27.5vh;
}
#daily-quotes>.card-menubar {
	position: relative;
}
.schedule-section-time {
	width: 15%;
	font-size: 25px;
	margin-bottom: 5px;
	margin-left: 5px;
	text-align: left;
	font-weight: 700;
}
.schedule-section {
	display: grid !important;
	width: 15%;
}
.schedule-section>.section-card {
	width: 100%;
}

.section-list.activity-center {
	display: flex !important;
	flex-direction: row !important;
}

.activity-card {
	filter: unset;
	border: 1px solid #ccc;
	min-width: 100%;
	max-width: 100%;
	min-height: 64vh;
	max-height: 64vh;
}
.activity-card>.card-menubar {
	position: relative;
}
.activity-card>.card-content {
	display: flex;
	gap: 10px;
}

.completed-jobs {
	width: 40%;
	height: 100%;
	padding: 0;
	margin: 0;
}
.performance-metrics>.card-content {
	padding: 0;
	margin: 0;
	height: 95%;
	width: 37.5vw;
	border: 1px solid #f1f1f1;
	border-radius: 10px;
}
#progress-bar {
	height: 150px;
	width: 150px;
}
#completed-jobs-table, .performance-metrics {
	min-height: 50.5vh;
	padding: 0px;
}
#completed-jobs-table>.tablerow {
	height: 40px;
	grid-template-columns: 30% 30% 30% !important;
	padding: 0;
}
#completed-jobs-table>.tablerow:hover {
	background-color: #f1f1f1;
}
#completed-jobs-table {
	border: 1px solid #f1f1f1;
	border-bottom-right-radius: 10px;
	border-bottom-left-radius: 10px;
}
.completed-jobs>.tablerow.header>div {
	padding: 0;
	margin: 0;
	background-color: transparent;
	line-height: 40px;
	font-size: 1.075em;
	color: #505050;
	font-weight: 600;
}
.completed-jobs>.tablerow.header {
	grid-template-columns: 30% 30% 40% !important;
	padding: 0;
	margin: 0;
	height: 40px !important;
	border-top-right-radius: 10px;
	border-top-left-radius: 10px;
	border: 1px solid #f1f1f1;
	background-color: #f1f1f1;
	border-bottom: 0px;
}
#completed-jobs-header>.header-text {
	color: rgb(30, 30, 30);
	font-size: 1.5em;
	margin-left: 10px;
}

/*NOTIFICATIONS*/
.notification-list {
	display: flex;
	flex-direction: column;
	gap: 10px;
	overflow-y: overlay;
	width: 100%;
}
.notification {
	background-color: #fff;
	border-radius: 10px;
	display: grid;
	grid-template-columns: 75% 25% !important;
	border: 1px solid #efefef;
}
.notification-content {
	text-align: left;
	background-color: #efefef;
	padding: 20px;
	border-radius: 10px;
}
.notification-type {
	font-size: 30px;
}
.notification-text {
	font-style: italic;
}
.notification-actions {
	row-gap: 0px;
	align-items: center;
	justify-items: center;
}
.notification-actions>.action-row {
	width: 90%;
	margin-left: 5%;
	margin-right: 5%;
	margin-top: 10%;
}
.notification-dismiss {
	position:relative;
	top:0px;
	left:80.5%;
	height: 20%;
	font-weight: 500;
	cursor: pointer;
	width: min-content;
	padding-left: 10px;
	padding-right: 10px;
	border-top-right-radius: 5px;
}

.notification-dismiss:hover {
	font-weight: 900;
	background-color: #ccc;
}

.notification-count {
	position: absolute;
	pointer-events: none;
	top: 50px;
	left: 130px;
	background-color: #c93030;
	color: #fff;
	width: 20px;
	height: 20px;
	border-radius: 30px;
}
.notification-count.collapsed {
	left: 30px;
}

/*COMPLETED QUOTES TABLE*/
#completed-quotes-card {
	min-width: 98.5% !important;
	max-width: 98.5% !important;
	min-height: 91.5vh;
	max-height: 91.5vh;
}
#completed-quotes-card-card-content {
	display: flex;
	flex-direction: column;
	padding: 0;

}
#completed-quotes-card-card-content>.tablerow.header {
	background-color: #062a49;
	color:#fff;
	border-top-right-radius: 7.5px;
	border-top-left-radius: 7.5px;
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: repeat(6, 16.5%);
}
#completed-quotes-card-card-content>.tablerow.header>div {
	padding: 0;
	margin: 0;
	line-height: 40px;
	font-size: 20px;
	background-color: transparent;
}

#completed-quotes-card-card-content>.card-content.data-table {
	min-height: 83.5vh;
	max-height: 83.5vh;
	overflow-y: overlay;
	padding: 0;
}
#completed-quotes-card-card-content>.card-content.data-table>:first-child {
	border-top: 1px solid #ccc;
}
#completed-quotes-card-card-content>.card-content.data-table>.tablerow {
	height: 10%;
	background-color: transparent;
	display: grid;
	grid-template-columns: repeat(6, 16.5%);
	border-bottom: 1px solid #ccc;
}
#completed-quotes-card-card-content>.card-content.data-table>.tablerow>div {
	width: max-content;
	justify-self: center;
}

#completed-quotes-card-card-content>.card-content.data-table>.tablerow:hover {
	background-color: #ccc;
	border-bottom: 2px solid #43bc90;
}
#completed-quotes-card-card-content>.card-content.data-table>.tablerow>:last-child {
	border-bottom: 2px solid #43bc90;
	margin-top: 5px;
}
#completed-quotes-card-card-content>.card-content.data-table>:first-child:hover {
	border-bottom: 2px solid #bc4343;
}
#completed-quotes-card-card-content>.card-content.data-table>:first-child>:last-child {
	border-bottom: 2px solid #bc4343;
}
#completed-quotes-card-card-content>.card-content.data-table>:last-child:hover {
	border-bottom: 2px solid #bc8743;
}
#completed-quotes-card-card-content>.card-content.data-table>:last-child>:last-child {
	border-bottom: 2px solid #bc8743;
}

/*VIEWS*/
/*@todo: Style for collapsed views*/
.left-view {
	display: flex;
	flex-direction: column;
	width: 65%;
	gap: 10px;
}
.right-view {
	width: 32.5%;
}


/*tableting: landscape*/
@media (orientation:landscape) and (max-width: 1400px) {
	.left-view {
		/*background-color: #ff079c;*/
	}
	.activity-center {
		overflow-y: hidden;
	}
	#daily-quotes {
		min-height: 35vh;
		max-height: 35vh;
	}
	.section-list.activity-center {
		width: 100% !important;
		height: 50% !important;
		display: grid !important;
		overflow-y: overlay;
		grid-template-columns: 47.5% 47.5% !important;
	}
	.section-list.activity-center>.schedule-section {
		width: 100%;
	}
	.section.quote-section.open {
		width: 100%;
	}
	.section-card {
		display: flex;
		padding: 0;
		margin: 0;
		height: 21.5vh !important;
	}
	.quote-actions.action-row.center {
		position: relative;
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
	}

	#metrics-card {
		min-height: 55vh;
		max-height: 55vh;
	}


	#notifications-card {
		min-height: 91vh !important;
		max-height: 91vh !important;
	}
	.notification-list {
		height: 82vh;
	}
	.notification {
		height: 17.5%;
	}
	.notification-type {
		font-size: 20px;
	}
	.notification-text {
		font-size: 15px;
	}
	.notification-actions>.action-row {
		flex-direction: column;
	}
	.notification-actions>.action-row>.action-button {
		width: 100%;
	}
	.notification-dismiss {
		left: 71%;
	}

	#quote-card {
		min-height: 82.5vh;
		max-height: 82.5vh;
		min-width: 60vw;
		max-width: 60vw;
	}
	#quote-card-client-info {
		max-height: fit-content;
	}
	#quote-card-actions {
		height: max-content;
	}

	.quote-sections {
	}
	.quote-sections>.section.quote-section.open>.section-list {
		display: grid !important;
		grid-template-columns: repeat(4, 25%);
		width: 95%;
	}
	.quote-sections>.section.quote-section.open>.section-list>.section-card {
		width: 100%;
	}
}

/*tableting: portrait*/
@media (orientation:portrait) {
	.activity-center {
		flex-direction: column;
		overflow-y: overlay !important;
	}
	.left-view {
		width: 97.5%;
	}
	.left-bottom-view>.card.activity-card {
		min-height: 30vh !important;
		max-height: 30vh !important;
	}
	.right-view {
		width: 97.5%;
	}
	.section.quote-section.open {
		width: 100%;
		height: 22vh;
	}
	.section-list.activity-center {
		width: 100% !important;
		height: 100%;
	}
	.section-list.activity-center>.schedule-section {
		width: 24%;
		max-height: min-content;
	}
	.section-card {
		display: flex;
		padding: 0;
		margin: 0;
	}
	.quote-actions.action-row.center {
		position: relative;
		bottom: -10px;
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
	}
	.quote-actions.action-row.center>.action-button {
		overflow: hidden;
	}
	#notifications-card {
		min-height: 35vh !important;
		max-height: 35vh !important;
	}
	.notification-dismiss {
		left: 85.1%;
		border-top-right-radius: 5px;
	}

	#quote-card {
		min-height: 60vh;
		max-height: 60vh;
		min-width: 95vw;
		max-width: 95vw;
	}
	#quote-card-actions {
		height: max-content;
	}
	.quote-info-section-card {
		min-width: 100%;
		max-width: 100%;
	}

	.quote-sections>.section.quote-section.open>.section-list {
		display: grid !important;
		grid-template-columns: repeat(3, 33%);
		width: 95%;
	}
	.quote-sections>.section.quote-section.open>.section-list>.section-card {
		width: 100%;
	}
}


/*Scrollbar stuff*/

*::-webkit-scrollbar {
    display: block;
    width: 8px;
}
*::-webkit-scrollbar-button {
    display: none;
}
*::-webkit-scrollbar-track {
    background-color: #00000000;
}
*::-webkit-scrollbar-track-piece {
    background-color: #00000000;
}
*::-webkit-scrollbar-thumb {
    background-color: #000000c7;
    border: 1px solid #00000040;
    border-radius: 12px;
}